.search {
    input[type="text"] {
        width: 100%;
        height: 100%;
        background: transparent;
        border: none;
        outline: none;
        font-size: 20px;
        font-weight: lighter;
        color: var(--color-main);

        &::placeholder {
            color: var(--color-main-light);
        }
    }

    width: 100%;
    border: 2px solid var(--color-main-light);
    border-radius: 16px;
    padding: 20px 40px;
}

@media screen and (max-width: 768px) {
    .search {
        input[type="text"] {
            font-size: 16px;
        }

        padding: 10px 30px;
    }
}

@media screen and (max-width: 480px) {
    .search {
        input[type="text"] {
            font-size: 14px;
        }

        padding: 10px 20px;
    }
}
