.pairs {
    width: 100%;

    &__navbar {
        display: flex;
        gap: 40px;
        margin-top: 100px;
        margin-bottom: 80px;
    }

    &__no-pairs {
        display: flex;
        justify-content: center;
        color: var(--color-main-light);
    }
}

@media screen and (max-width: 768px) {
    .pairs {
        &__navbar {
            gap: 20px;
            margin-top: 60px;
            margin-bottom: 40px;
        }
    }
}
